/* Define grid areas */
.Main {
  grid-area: main; }

.Header {
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  display: -ms-flexbox;
  display: flex;
  grid-area: header;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 15px;
  text-align: center; }
  .Header .Logo {
    height: 30px; }
  .Header .Search-icon {
    height: 20px; }
    .Header .Search-icon:hover {
      cursor: pointer; }

.Footer {
  -ms-flex-align: center;
      align-items: center;
  background-color: #646464;
  color: #FFFFFF;
  display: -ms-flexbox;
  display: flex;
  grid-area: footer;
  -ms-flex-pack: distribute;
      justify-content: space-around; }
  .Footer .Item {
    -ms-flex-align: center;
        align-items: center;
    background-color: teal;
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
